@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #27AE60;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media only screen and (max-width: 480px) {
  body {
    width: 100vw;
    overflow: hidden;
  }
}


#quote-box {
  font-family: 'Raleway', Arial, Helvetica, sans-serif;
  font-size: 1.5rem;
  width: 30rem;
  height: 20rem;
  background-color: #fff;
  color: #27AE60;
  position: relative;
  padding: 1rem;
  border-radius: 0.2rem;
}

#text {
  width: 28rem;
  height: 8rem;
  margin: auto;
}

#author {
  width: 16rem;
  height: 2.5rem;
  position: absolute;
  top: 14rem;
  left: 19.5rem;
  font-size: 1rem;
  text-align: left;
}

.flex {
  width: 27rem;
  height: 3rem;
  margin: 0;
  display: flex;
  justify-content: space-between;
  position: absolute;
  bottom: 0;
  left: 50%;
  right: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

#tweetLink {
  font-size: 1.5rem;
  background-color: #27AE60;
  width: 3rem;
  height: 3rem;
  border-radius: 0.2rem;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  height: 8vh;
}

i {
  color: #fff
}

span > i {
  color: #27AE60;
}

#new-quote {
  font-size: 1rem;
  background-color: #27AE60;
  color: #fff;
  padding: 0.6rem;
  width: 7rem;
  height: 3rem;
  border-radius: 0.2rem;
  border: none;
}

@media only screen and (max-width: 590px) {
  #quote-box {
    font-size: 1rem;
    width: 80vw;
    height: 50vh;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  #text {
    margin-top: 1rem;
    width: auto;
    height: auto;
  }

  .flex {
    width: 95%;
    height: 3rem;
    padding: 0.5rem;
    margin: 0 auto;
    display: flex;
  }

  #author {
    margin: 0 auto;
    text-align: center;
    position: absolute;
    top: 9rem;
    left: 50%;
    right: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }

  #new-quote {
    font-size: 1rem;
    padding: 0.4rem;
    width: 6rem;
    height: 2.5rem;
    
  }

  

  #tweetLink {
    font-size: 1.5rem;
    width: 3rem;
    height: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    
  }
  
}


